.vidCont .img{
    width: clamp(180px , 14vw , 17vw);
    height: clamp(125px , 10vh , 14vh);
    background-color: rgba(0, 0, 0,0.5);
    margin: 0px;
}
.vidCont .img img{
    width: 100%;
    height: 100%;
}
.vidCont {
    width: clamp(180px , 14vw , 17vw);
    margin: 5px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: blanchedalmond;
}
.vidCont :is(h1 , h3 ){
    font-size: clamp(10px , 1.3vw , 2vw);
    padding: 2.5px;
    margin: 0;
    text-align: center;
}
.vidCont :is(h3){
    font-size: clamp(7px , 1vw , 1.5vw);
    text-align: start;
}